header.header {
  display: flex;
  justify-content: center; }
  header.header img.header__logo {
    display: block;
    width: 75%;
    min-width: 376px;
    max-width: 676px;
    padding: 1.2rem; }

main.giphy {
  display: flex;
  justify-content: center;
  margin: 1.2rem; }
  main.giphy form.search {
    display: flex;
    justify-content: center;
    width: 80%;
    min-width: 376px;
    max-width: 676px; }
    main.giphy form.search input.search__field {
      width: calc(100% - 100px);
      padding: .9rem;
      border: 2px solid #AB41C1;
      font-size: 16px; }
    main.giphy form.search button.search__button {
      padding: .9rem;
      border: 2px solid #AB41C1;
      background-color: #AB41C1;
      border-left-width: 0px;
      color: #fff; }
      main.giphy form.search button.search__button:hover {
        background-color: #bf49d6; }

footer.footer p.footer__copyright {
  text-align: center; }

footer.footer a {
  color: #AB41C1; }
